.jumbotron {
  min-height: 200px;
  width: 100%;
  /* background: linear-gradient(to bottom right, #0060DF 0%, #18ABFB 50%, #0060DF 100% ); */
  background: -webkit-gradient(linear,  left top, right bottom,  from(rgb(1, 62, 141)), to(#18ABFB) );
  background: -webkit-linear-gradient(top left,  rgb(1, 62, 141) 0%, #18ABFB 100% );
  background: -o-linear-gradient(top left,  rgb(1, 62, 141) 0%, #18ABFB 100% );
  background: linear-gradient(to bottom right,  rgb(1, 62, 141) 0%, #18ABFB 100% );
  /* background: black; */
  color: white !important;
  padding: 50px; 
}





